<script>
export default {
  data () {
    return {
      products: [],
      pagination: {}
    }
  },
  methods: {
    getProducts () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`
      this.$http.get(url)
        .then(res => {
          console.log(res)
          this.products = res.data.products
          this.pagination = res.data.pagination
        })
    }
  },
  mounted () {
    this.getProducts()
  }
}
</script>
